
body {
  margin: 0;
  padding: 0;
  background: #eee;
}
.container {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.game-container {
  position: relative;
  width: 800px;
  height: 500px;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}
.btn {
  cursor: pointer;
  padding: 10px 15px;
  background: #eee;
}
.stop {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.notice {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px;
  background: #000;
  font-weight: 100;
  width: 120px;
  color: #fff;
}
.block {
  background: #000;
  float: left;
}
.game-main-window {
  position: absolute;
  height: 100%;
  left: 0;
  right: 0;
  overflow: hidden;
}
.game-wrapper {
  position: relative;
  width: 10000%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: end;
      align-items: flex-end;
  transform: translate(0, 0);
}
.player {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.over-mask {
  position: absolute;
  left: 0;
  top: 0;
  background: #000;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 1s;
}



